"use client";
import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Phone, Menu, X, Navigation, ChevronDown } from 'lucide-react';

const Navbar = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [activeDropdown, setActiveDropdown] = useState(null);
  const phoneNumber = "548-388-4586";

  // Data blueprint for the 18-page silo structure
  const menuStructure = [
    {
      label: "Roadside Assistance",
      links: [
        { name: "24 Hour Towing", slug: "24-hour-towing-ottawa", isService: true },
        { name: "Battery Jump Start", slug: "battery-jump-start-ottawa", isService: true },
        { name: "Out-of-Gas Service", slug: "out-of-gas-ottawa", isService: true },
      ]
    },
    {
      label: "Accident & Collision",
      links: [
        { name: "Electric Car Towing", slug: "electric-car-towing-ottawa", isService: true },
        { name: "Flatbed Towing", slug: "flatbed-towing-ottawa", isService: true },
        { name: "Heavy Duty Towing", slug: "heavy-duty-towing-ottawa", isService: true },
        { name: "Motorcycle Towing", slug: "motorcycle-towing-ottawa", isService: true },
        { name: "Winching Service", slug: "winching-service-ottawa", isService: true },
      ]
    },
    {
      label: "Specialty Towing",
      links: [
        { name: "Corporate Towing", slug: "corporate-towing-ottawa", isService: true },
        { name: "Long-Distance Towing", slug: "long-distance-towing-ottawa", isService: true },
        { name: "Private Towing Service", slug: "private-towing-ottawa", isService: true },
        { name: "Vehicle Storage", slug: "vehicle-storage-ottawa", isService: true },
      ]
    },
    {
      label: "Our Company",
      links: [
        { name: "About Us", slug: "about-us", isService: false },
        { name: "Reviews", slug: "reviews", isService: false },
        { name: "Gallery", slug: "gallery", isService: false },
        { name: "FAQs", slug: "faqs", isService: false },
        { name: "Blogs", slug: "blogs", isService: false },
      ]
    }
  ];

  return (
    <nav className="fixed top-0 left-0 w-full bg-slate-950/60 backdrop-blur-xl border-b border-white/5 z-[100]">
      <div className="max-w-7xl mx-auto px-6 h-20 flex items-center justify-between">
        
        {/* LOGO AREA */}
        <a href="/" className="flex items-center gap-2 group">
          <div className="bg-amber-500 p-1.5 rounded text-slate-900 group-hover:bg-white transition-colors">
            <Navigation size={22} fill="currentColor" />
          </div>
          <div className="flex flex-col items-start leading-none">
            <span className="text-xl font-black tracking-tighter italic text-white uppercase">
              OTTAWA<span className="text-amber-500">TOWING</span>
            </span>
            <span className="text-[9px] uppercase tracking-[0.2em] font-bold text-slate-400">
              SOLUTIONS
            </span>
          </div>
        </a>

        {/* DESKTOP LINKS WITH DROPDOWNS */}
        <div className="hidden lg:flex items-center gap-6">
          {menuStructure.map((group) => (
            <div 
              key={group.label}
              onMouseEnter={() => setActiveDropdown(group.label)}
              onMouseLeave={() => setActiveDropdown(null)}
              className="relative py-4"
            >
              <button className="flex items-center gap-1 text-xs font-black uppercase tracking-widest text-slate-300 hover:text-amber-500 transition-colors">
                {group.label} 
                <ChevronDown size={14} className={`transition-transform duration-300 ${activeDropdown === group.label ? 'rotate-180' : ''}`} />
              </button>

              <AnimatePresence>
                {activeDropdown === group.label && (
                  <motion.div 
                    initial={{ opacity: 0, y: 10, scale: 0.95 }}
                    animate={{ opacity: 1, y: 0, scale: 1 }}
                    exit={{ opacity: 0, y: 10, scale: 0.95 }}
                    className="absolute top-full left-0 w-64 bg-slate-900/95 backdrop-blur-2xl border border-white/10 rounded-2xl p-3 shadow-2xl"
                  >
                    {group.links.map((link) => (
                      <a 
                        key={link.slug} 
                        href={link.isService ? `/services/${link.slug}` : `/${link.slug}`}
                        className="block px-4 py-2.5 text-sm font-bold text-slate-400 hover:text-white hover:bg-white/5 rounded-xl transition-all"
                      >
                        {link.name}
                      </a>
                    ))}
                  </motion.div>
                )}
              </AnimatePresence>
            </div>
          ))}
          
          <a 
            href={`tel:${phoneNumber}`} 
            className="flex items-center gap-2 bg-amber-500 text-slate-900 px-6 py-3 rounded-2xl hover:bg-white transition-all shadow-xl shadow-amber-500/20 font-black italic uppercase text-sm"
          >
            <Phone size={18} fill="currentColor" />
            <span>{phoneNumber}</span>
          </a>
        </div>

        {/* MOBILE HAMBURGER */}
        <button onClick={() => setIsOpen(!isOpen)} className="lg:hidden p-2 text-white">
          {isOpen ? <X size={28} /> : <Menu size={28} />}
        </button>
      </div>

      {/* MOBILE MENU (Needs sub-menu logic too, but keeping basic for now) */}
      <AnimatePresence>
        {isOpen && (
          <motion.div
            initial={{ opacity: 0, x: 100 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: 100 }}
            className="lg:hidden fixed inset-0 top-20 bg-slate-950 p-6 overflow-y-auto"
          >
            {menuStructure.map((group) => (
              <div key={group.label} className="mb-8">
                <h3 className="text-amber-500 text-xs font-black uppercase tracking-widest mb-4">{group.label}</h3>
                <div className="flex flex-col gap-4 pl-4 border-l border-white/10">
                  {group.links.map((link) => (
                    <a 
                      key={link.slug} 
                      href={link.isService ? `/services/${link.slug}` : `/${link.slug}`}
                      className="text-xl font-black italic uppercase text-white"
                      onClick={() => setIsOpen(false)}
                    >
                      {link.name}
                    </a>
                  ))}
                </div>
              </div>
            ))}
          </motion.div>
        )}
      </AnimatePresence>
    </nav>
  );
};

export default Navbar;